/*
utils/request.js
ajax/
@author jinhuajie <jinhuajie@haotang365.com.cn>

@api
options.concurrent urltrue

200javacode===0thencode===0code0catch
*/

import ajax from '@/utils/ajax'

// 
let ajaxArray = []

export default (options = {}) => {
    return new Promise((resolve, reject) => {
    //  
        let ajaxUrl = options.url
        if (!options.concurrent) {
            ajaxUrl = ajaxUrl.split('?')[0]
            if (ajaxArray.indexOf(ajaxUrl) !== -1) {
                reject(new Error(` ${ajaxUrl}`))
                return
            }
            ajaxArray.push(ajaxUrl)
        }
        ajax(options).then((res) => {
            if (res.code == 200) {
                // code===0
                resolve(res)
            } else if (res.errno === 0) {
                resolve(res)
            } else {
                reject(res)
            }
        }).catch((err) => {
            reject(err)
        }).finally(() => {
            // 
            const i = ajaxArray.indexOf(ajaxUrl)
            if (i !== -1) ajaxArray.splice(i, 1)
        })
    })
}



