import request from '@/utils/request'
const requestDomain = envConfig.requestDomain

// 
export const getClub = data => {
    return request({
        url: `${requestDomain}/club-room/admin/getClub`,
        data: data,
        token: sessionStorage.getItem("token")
    })
}
