<template>
    <div class="container">
        <div class="user-message">
            <div class="user-box">
                <div class="user-header">
                    <img :src="userData.headPic" alt="">
                </div>
                <table class="user-data">
                    <thead>
                        <tr>
                            <th>ID</th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td>{{userData.id}}</td>
                            <td>{{userData.nikeName}}</td>
                            <td>{{userData.cellPhone}}</td>
                            <td>{{userData.createTime}}</td>
                            <td>{{userData.offLineTime}}</td>
                            <td>{{userData.totalTime}}</td>
                            <td>{{userData.createClubCount}}</td>
                            <td>{{userData.followClubSize}}</td>
                        </tr>
                    </tbody>
                </table>
                <div class="user-invite">
                    <div class="invite-msg">
                        <span></span>
                        <span></span>
                    </div>
                    <img src="https://ss0.bdstatic.com/70cFvHSh_Q1YnxGkpoWK1HF6hhy/it/u=2631334549,246605465&fm=26&gp=0.jpg" alt="">
                </div>
            </div>
            <div class="userdetail-info">
                <div></div>
                <div>
                    <br />
                    <br />
                    <br />
                </div>
            </div>
        </div>
        <div class="list-box">
            <div class="tab">
                <div :class=" currentTab == 1 ? 'tab-item active': 'tab-item'" @click="changeTab(1)"> <span>{{total1}}</span></div>
                <div :class=" currentTab == 2 ? 'tab-item active': 'tab-item'" @click="changeTab(2)"> <span>{{total2}}</span></div>
                <div :class=" currentTab == 3 ? 'tab-item active': 'tab-item'" @click="changeTab(3)"> <span>{{total3}}</span></div>
                <div :class=" currentTab == 4 ? 'tab-item active': 'tab-item'" @click="changeTab(4)"> <span>{{total4}}</span></div>
            </div>
            <div class="tab-content">  
                <div class="user-box">  
                    <el-table v-if="currentTab == 1 ? true : false" class="list-table"
                        :data="tableData1"
                        highlight-current-row
                        :cell-class-name="tdlist"
                        style="width: 100%">
                        <el-table-column prop="headPic" label="" width="120">
                            <template slot-scope="scope">
                                <img :src="scope.row.headPic" class="td-header" width="50" height="50" alt="">
                            </template>
                        </el-table-column>
                        <el-table-column prop="id" label="ID" width="120"></el-table-column>
                        <el-table-column prop="nikeName" label="" width="120"></el-table-column>
                        <el-table-column prop="cellPhone" label="" width="120"></el-table-column>
                        <el-table-column prop="createTime" label="" width="120"></el-table-column>
                        <el-table-column prop="offLineTime" label="" width="120"></el-table-column>
                        <el-table-column prop="followSize" label="" width="120"></el-table-column>
                        <el-table-column prop="fansSize" label="" width="120"></el-table-column>
                        <el-table-column prop="followClubSize" label="" width="120"></el-table-column>
                        <el-table-column prop="totalTime" label="" width="120"></el-table-column>
                        <el-table-column prop="createClubCount" label="" width="120"></el-table-column>
                    </el-table>
                    <el-table v-if="currentTab == 2 ? true : false" class="list-table"
                        :data="tableData2"
                        highlight-current-row
                        :cell-class-name="tdlist"
                        style="width: 100%">
                        <el-table-column prop="headPic" label="" width="120">
                            <template slot-scope="scope">
                                <img :src="scope.row.headPic" class="td-header" width="50" height="50" alt="">
                            </template>
                        </el-table-column>
                        <el-table-column prop="id" label="ID" width="120"></el-table-column>
                        <el-table-column prop="nikeName" label="" width="120"></el-table-column>
                        <el-table-column prop="cellPhone" label="" width="120"></el-table-column>
                        <el-table-column prop="createTime" label="" width="120"></el-table-column>
                        <el-table-column prop="offLineTime" label="" width="120"></el-table-column>
                        <el-table-column prop="followSize" label="" width="120"></el-table-column>
                        <el-table-column prop="fansSize" label="" width="120"></el-table-column>
                        <el-table-column prop="followClubSize" label="" width="120"></el-table-column>
                        <el-table-column prop="totalTime" label="" width="120"></el-table-column>
                        <el-table-column prop="createClubCount" label="" width="120"></el-table-column>
                    </el-table>
                    <el-table v-if="currentTab == 3 ? true : false" class="list-table"
                        :data="tableData3"
                        highlight-current-row
                        :cell-class-name="tdlist"
                        style="width: 100%">
                        <el-table-column prop="avatar" label="" width="120">
                            <template slot-scope="scope">
                                <img :src="scope.row.avatar" class="td-header" width="50" height="50" alt="">
                            </template>
                        </el-table-column>
                        <el-table-column prop="id" label="ID" width="120"></el-table-column>
                        <el-table-column prop="name" label="" width="120"></el-table-column>
                        <el-table-column prop="phone" label="" width="120"></el-table-column>
                        <el-table-column prop="email" label="" width="120"></el-table-column>
                        <el-table-column prop="enterTime" label="" width="120"></el-table-column>
                        <el-table-column prop="logintime" label="" width="120"></el-table-column>
                        <el-table-column prop="attion" label="" width="120"></el-table-column>
                        <el-table-column prop="fensi" label="" width="120"></el-table-column>
                        <el-table-column prop="club" label="" width="120"></el-table-column>
                        <el-table-column prop="timelone" label="" width="120"></el-table-column>
                    </el-table>
                    <el-table v-if="currentTab == 4 ? true : false" class="list-table"
                        :data="tableData4"
                        highlight-current-row
                        :cell-class-name="tdlist"
                        style="width: 100%">
                        <el-table-column prop="headPic" label="" width="120">
                            <template slot-scope="scope">
                                <img :src="scope.row.headPic" class="td-header" width="50" height="50" alt="">
                            </template>
                        </el-table-column>
                        <el-table-column prop="id" label="ID" width="120"></el-table-column>
                        <el-table-column prop="nikeName" label="" width="120"></el-table-column>
                        <el-table-column prop="cellPhone" label="" width="120"></el-table-column>
                        <el-table-column prop="createTime" label="" width="120"></el-table-column>
                        <el-table-column prop="offLineTime" label="" width="120"></el-table-column>
                        <el-table-column prop="followSize" label="" width="120"></el-table-column>
                        <el-table-column prop="fansSize" label="" width="120"></el-table-column>
                        <el-table-column prop="followClubSize" label="" width="120"></el-table-column>
                        <el-table-column prop="totalTime" label="" width="120"></el-table-column>
                        <el-table-column prop="createClubCount" label="" width="120"></el-table-column>
                    </el-table>
                </div>
                <div class="block">
                    <el-pagination
                    :background="true"
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-sizes="[10, 20, 30]"
                    :page-size="pageSize"
                    :pager-count="9"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="total">
                    </el-pagination>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
    import { userDetail } from '@/api/user/user.js'
    export default{ 
        name: 'userDetail',
        data() {
            return {
                ruleForm: {
                    phone: ''
                },
                rules: {
                    phone: [
                        { required: true, message: '', trigger: 'blur' }
                    ]
                },
                userData: {}
                ,
                tableData1: [],    
                tableData2: [],
                tableData3: [],
                tableData4: [],
                tdlist: 'tdlist',
                thnonebober:'thnonebober',
                tdbgc: 'tdbgc',
                total1: 0,
                total2: 0,
                total3: 0,
                total4: 0,
                currentPage: 1,
                pageSize: 2,
                currentTab: 1
            }
        },
        created() {
            this.userData = this.$store.state.user.currentUserdata;
            this.getuserDetail();
        },
        methods: {
            handleSizeChange(val){
                console.log(val);
                this.pageSize = val;
                this.getuserDetail();
            },
            handleCurrentChange(val){
                console.log(val)
                this.currentPage = val;
                this.getuserDetail();
            },
            changeTab(index){
                this.currentTab = index;
                this.currentPage = 1;
                this.pageSize = 2;
                this.getuserDetail();
            },
            // 
            getuserDetail(){
                let data = {
                    pageNo: this.currentPage,
                    pageSize: this.pageSize,
                    type: this.currentTab,
                    userId: this.userData.id
                }
                userDetail(data).then(res =>{
                    if(res.code == 200 && res.data){
                        this.tableData[this.currentTab] = this.data;
                        this.total[this.currentTab] = res.total;
                    }
                }).catch(err =>{
                    this.$message.error(err.msg);
                })
            }
        },
    }
</script>
<style lang="stylus" scoped>
    .container
        width 100%;
        background-color #F5F7F9;
        // padding-bottom: 30px;
    .header-table
        width 100%;
        height 112px;     
        padding 0 10px!important;
    .tdbgc
        padding 24px 0!important;
        border-bottom: none!important;
    .list-table
        padding 0 20px!important;
    .check
        background-color: #7DBFBB!important;
        color: #fff;
    .block
        margin-top 20px
        margin-bottom 30px;
    /deep/  .thnonebober
                border-bottom: none!important;
    .user-message
        width 100%;
        padding 20px;
        box-sizing: border-box;
        margin-bottom 30px;
        background-color #fff;
        border-radius: 10px;
        box-sizing: border-box;
        > .user-box
            width 100%;
            overflow-x: auto;
            padding-bottom: 10px;
            display flex;
            align-items center;
            justify-content: flex-start;
            margin-bottom: 20px;
            > .user-data
                width: 1125px;
                // height: 124px;
                margin-left: 10px;
                > thead th
                    font-family: PingFangSC-Medium;
                    font-size: 16px;
                    color: #002241;
                    letter-spacing: 0;
                    line-height: 16px;
                    padding: 20px 20px;
                    box-sizing: border-box;
                > tbody td
                    font-family: PingFangSC-Medium;
                    font-size: 14px;
                    color: #717985;
                    text-align: center;
                    line-height: 14px;
                    padding: 20px;
            // box-shadow 
        > .userdetail-info
            width: 100%;
            height: 154px;
            background: #F5F4F5;
            border-radius: 10px;
            overflow: hidden;
            // margin-bottom: 20px;
            > div:first-of-type
                font-family: PingFangSC-Medium;
                font-size: 16px;
                color: #002241;
                letter-spacing: 0;
                text-align: left;
                line-height: 16px;
                margin: 20px 25px;
            > div:last-of-type
                font-family: PingFangSC-Regular;
                font-size: 14px;
                color: #002241;
                letter-spacing: 0;
                line-height: 26px;
                text-align: left;
                padding-left: 25px;
    .user-header
        >img 
            width 100px;
            height 100px;
            border-radius: 50%; 
    .user-invite
        width 238px;
        height 124px;
        padding: 12px 20px;
        box-sizing: border-box;
        display flex;
        justify-content center;
        align-items center;
        background: #F5F4F5;
        border-radius: 70px;
        > img 
            width 80px;
            height 80px;
            border-radius 50%;   
            margin-left 30px
        > div   
            margin-left: 20px
            > span  
                display: block;
                width: 48px;
                font-size 16px 
                font-family: PingFangSC-Medium;
                color: #002241;
            > span:last-of-type
                margin-top 35px
                color: #1B8668;
    .list-box
        width 100%;
        background-color: #fff;
        overflow:hidden;
        .tab
            width 100%;
            display flex;
            justify-content flex-start;
            align-items center;
            margin: 30px 0 20px 30px;
            // margin-bottom 20px;
            > .tab-item 
                // width 2.00px;
                height 44px;
                background: #E7EBF4;
                text-align: center;
                margin-right 30px;
                font-weight bolder;
                border-radius 4px; 
                cursor pointer;
                font-family: PingFangSC-Medium;
                font-size: 16px;
                color: #9FA7B9;
                letter-spacing: 0;
                box-sizing: border-box;
                padding 12px 20px;
            > .tab-item.active
                background-color #7DBFBB;
                color: #fff;
        .tab-content
            width 100%;
            background-color #fff;
            > .user-box
                width 100%;
                display flex;
                align-items center;
        .tdlist .td-header
            border-radius 50%;
    .block
        background-color: #fff;
</style>



